import e from 'cors';
import React, { Component } from 'react'
import {Link, Redirect} from 'react-router-dom'
import { setInStorage } from '../utils/storage';

import {register, login} from '../utils/authfuncs';

import './Home.css';
import { MyContext } from '../storage';
import App from '../App';

export class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            register: this.props.register,
            name: '',
            email: '',
            password: '',
            redirect: false
        };

        this.onChange = this.onChange.bind(this);
        this.select1 = this.select1.bind(this);
        this.select2 = this.select2.bind(this);
        this.onSubmit = this.onSubmit.bind(this);
    }

    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    select1() {
        this.setState({
            register: false
        });
    }

    select2() {
        this.setState({
            register: true
        });
    }

    onSubmit(e) {
        const {
            name,
            email,
            password
        } = this.state;
        e.preventDefault();
        if (this.state.register) {
            const newUser = {
                name,
                email,
                password
            };
            //console.log(this.context);
            register(this.context.state.proxy, newUser, (data) => {
                login(this.context.state.proxy, newUser, (data2) => {
                    const {
                        email,
                        name,
                        conversations,
                        tag,
                        _id
                    } = data.user;
                    //console.log(this.context);
                    this.context.setUserData({
                        email,
                        name,
                        conversations,
                        _id,
                        tag
                    });
                    
                    setInStorage('Epicenter', {token: data2.token});
                    this.context.changeToken(data2.token);
                    
                    this.setState({
                        name: '',
                        email: '',
                        password: '',
                        redirect: true
                    });
                    
                    this.props.changeToken(data2.token);
                })
            });
        } else {
            const info = {
                email,
                password
            };
            login(this.context.state.proxy, info, (data2) => {
                //console.log(data2);
                const {
                email,
                name,
                conversations,
                tag,
                _id
                } = data2.user
                //console.log(this.context, 1)
                this.context.setUserData({
                email,
                name,
                conversations,
                tag,
                _id
                });
                //console.log(this.context, 2)

                setInStorage('Epicenter', {token: data2.token});
                this.context.changeToken(data2.token);
                this.props.changeToken(data2.token);
                //console.log(this.context, 3)
                this.setState({
                name: '',
                email: '',
                password: '',
                redirect: true
                });
                
            })
        }
    }

    render() {
        
        if (this.state.redirect) {
            return (<Redirect to="/chat" />)
        }
        
        return (
            <div id="body">
                <div className="navbar">
                    <div className="container flex">
                        <a href="index.html"><h1 className="logo">Epicenter</h1></a>
                        <nav>
                            <ul>
                                <li><a href="#home">Home</a></li>
                                <li><a href="#downloads">Download</a></li>
                                <li><a href="#login-register">Login/Register</a></li>
                            </ul>
                        </nav>
                    </div>
                </div>

                <div className="main">
                        <div id="show-wrapper" className="show-wrapper stuff">
                            <div id="home">
                                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#B35AA8" fillOpacity="1" d="M0,96L1440,320L1440,320L0,320Z"></path></svg>
                                <div className="title-wrapper">
                                    <h1 id="showcase-title">The Perfect<br />Place To<br />Talk.</h1>
                                    <p>Epicenter is the best place to chat with friends!</p>
                                    <center>
                                    <a href="#login-register"><button>Start Chatting</button></a>
                                    </center>
                                </div>
                            </div>
                            <div id="downloads">
                                <div id="section2">
                                    <div id="blackthing"></div>
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#333" fillOpacity="1" d="M0,288L1440,160L1440,320L0,320Z"></path></svg>
                                    <div className="title-wrapper">
                                        <h1 id="showcase-title">We are currently not offering any downloads.</h1>
                                        <p>Stay tuned for upcoming releases!</p>
                                    </div>
                                </div>
                            </div>
                            <div id="login-register">
                                <div id="auth-wrapper" className="auth-wrapper stuff">
                                    <div className="auth">
                                        <div className={this.state.register ? 'label1' : 'label1 auth-selected'} onClick={this.select1}>Login</div>
                                        <div className={this.state.register ? 'label2 auth-selected' : 'label2'} onClick={this.select2}>Register</div>
                                        <div className="infoarea auth-selected">
                                        {this.state.register ? 
                                        <div>
                                            <label htmlFor="name">Name: </label><br />
                                            <input type="text" className="editinput" name="name" value={this.state.name} onChange={this.onChange}/>
                                        </div> : null}
                                        <div>
                                            <label htmlFor="username">Email: </label><br />
                                            <input type="email" className="editinput" name="email" value={this.state.email} onChange={this.onChange}/>
                                        </div>
                                        <div>
                                            <label htmlFor="username">Password: </label><br />
                                            <input type="password" className="editinput" name="password" value={this.state.password} onChange={this.onChange}/>
                                        </div>
                                        <div>
                                            <button onClick={this.onSubmit}>Submit</button>
                                        </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                
                </div>
            </div>
        )
    }
}

export default Home
Home.contextType = MyContext