import React, { Component } from 'react'

import socketIOClient from 'socket.io-client'

import {Link, withRouter} from 'react-router-dom'
import { MyContext } from '../storage';
import { getFromStorage } from '../utils/storage';

import './Chat.css'

export class Chat extends Component {
    
    constructor(props) {
        super(props);
        this.state = {
            redirect: false,
            new: false,
            name: '',
            convoid: this.props.match.params.id,
            conversation: {},
            isLoading: true,
            message: '',
            newFocused: false
        };

        this.socket = socketIOClient('https://epicenter-am.herokuapp.com');
        this.messagesEnd = React.createRef();
        this.inputRef = React.createRef();
        
        this.logout = this.logout.bind(this);
        this.onNew = this.onNew.bind(this);
        this.onChange = this.onChange.bind(this);
        this.onSend = this.onSend.bind(this);
        this.scrollToBottom = this.scrollToBottom.bind(this);
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }

    static getDerivedStateFromProps(nextProps, prevState) {
        return {convoid: nextProps.match.params.id}
    }

    

    componentDidMount() {
        if (!this.state.isLoading) {
            this.scrollToBottom();
            if (this.state.convoid) {
                this.inputRef.current.focus();
            }
        }
        this.socket.on('refresh-convo', () => {
            fetch(`${this.context.state.proxy}users/verify?token=${this.context.state.token}`)
                .then(response => response.json())
                .then(json => {
                    this.context.setUserData(json.user);
                })
        });
        if (this.state.convoid !== undefined) {
            this.context.state.userData.conversations.forEach(conversation => {
                if (conversation._id === this.state.convoid) {
                    this.socket.emit('join-room', this.state.convoid, this.context.state.userData._id);
                    //console.log(conversation);
                    
                    this.setState({
                        conversation: conversation,
                        isLoading: false
                    });
                }
            })
        } else {
            this.setState({
                isLoading: false
            });
        }
        //this.socket.emit('hello');
    }

    componentDidUpdate(prevProps, prevState) {
        if (!this.state.isLoading) {
            this.scrollToBottom();
            //console.log(1);
            if (!this.state.newFocused && this.state.convoid) {
                this.inputRef.current.focus();
            }
        }
        if (prevState.conversation._id !== this.state.convoid) {
            this.context.state.userData.conversations.forEach(conversation => {
                if (conversation._id === this.state.convoid) {
                    this.socket.emit('join-room', this.state.convoid, this.context.state.userData._id);
                    //console.log(conversation);
                    this.setState({
                        conversation: conversation,
                        isLoading: false
                    });
                }
            })
        }
    }

    onBlur() {
        this.setState({
            newFocused: false
        });
    }

    onFocus() {
        this.setState({
            newFocused: true
        });
    }
    

    onChange(e) {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    onNew(e) {
        e.preventDefault();
        if(this.state.new === false) {
            this.setState({
                new: true
            });
        } else {
            const {
                name
            } = this.state;

            let name1 = name.split("#")[0];
            let tag = name.split("#")[1];

            fetch(`${this.context.state.proxy}conversations/new`, {
                method: 'POST',
                headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
                },
                body: JSON.stringify({
                    name: name1,
                    tag,
                    userid: this.context.state.userData._id
                })
            }).then(res => res.json())
                .then(json => {
                if (json.success) {
                    console.log(json);
                    this.socket.emit('conversation-created');
                    this.props.history.push(`/${json.message._id}`);
                }
            })
        }
    }

    scrollToBottom() {
		//this.messagesEnd.current.scrollIntoView({behavior: 'smooth'});
        //console.log(this.messagesEnd);
	}

    onSend(e) {
        const author = this.context.state.userData.name;
        const text = this.state.message
        const convoid = this.state.convoid
        let date1 = new Date();
        e.preventDefault();
        
        fetch(`${this.context.state.proxy}conversations/send`, {
            method: 'POST',
            headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
            },
            body: JSON.stringify({
                author,
                text,
                convoid
            })
        }).then(res => res.json())
            .then(json => {
            if (json.success) {
                this.socket.emit('message-sent', this.state.convoid);
                
                this.setState({
                    message: ''
                });
            }
        })
    }

    logout(e) {
        e.preventDefault();
        const obj = getFromStorage('Epicenter');

        if (obj && obj.token) {
            fetch(this.context.state.proxy + 'users/logout?token=' + obj.token)
                .then(res => res.json())
                .then(json => {
                    if (json.success) {
                        this.props.setParentToken('');
                        this.context.changeToken('');
                        this.context.setUserData({});
                    }
                })
        }
    }

    render() {
        
        let {
            people,
            messages
        } = this.state.conversation;
        //console.log(this.state)
        if (this.state.isLoading === true) {
            return ('isLoading')
        }
        return (
            <div id="body">
                <div className="navbar">
                    <div className="container flex">
                        <a href="index.html"><h1 className="logo">Epicenter</h1></a>
                        <nav>
                            <ul>
                                
                                <li><Link to="/" onClick={this.logout}>Logout</Link></li>
                            </ul>
                        </nav>
                    </div>
                </div>

                <div className="main">
                        <div style={{overflow: 'hidden'}} id="show-wrapper2" className="show-wrapper stuff">
                            <main>
                                <div className="listnav">
                                    <h3>{this.context.state.userData.name}#{this.context.state.userData.tag}</h3>
                                    <hr />
                                    <nav>
                                    <ul>
                                        
                                        {
                                            this.context.state.userData.conversations.map((conversation, i) => {
                                                //console.log(conversation);
                                                if (conversation.people[0] === this.context.state.userData.name) {
                                                    return (
                                                        <Link key={i} to={'/chat/' + conversation._id}>
                                                            <li style={{backgroundColor: conversation._id === this.state.convoid ? 'rgb(61, 60, 60)' : null}}>
                                                                <p>
                                                                    {conversation.people[1]}
                                                                </p>
                                                            </li>
                                                        </Link>
                                                    )
                                                } else {
                                                    return (
                                                        <Link key={i} to={'/chat/' + conversation._id}>
                                                            <li style={{backgroundColor: conversation._id === this.state.convoid ? 'rgb(61, 60, 60)' : null}}>
                                                                <p>
                                                                    {conversation.people[0]}
                                                                </p>
                                                            </li>
                                                        </Link>
                                                    )
                                                }
                                            })
                                        }
                                                
                                        
                                        
                                        {
                                            this.state.new ? <li id="buttonwrap">
                                                <input onBlur={this.onBlur} onFocus={this.onFocus} name="name" onChange={this.onChange} value={this.state.name} type="text" className="new-input" placeholder="ex. Steve#0"/>
                                            </li> : null
                                        }
                                        <li id="buttonwrap">
                                            <button onClick={this.onNew} className="new">{this.state.new ? 'Create DM' : 'New DM +'}</button>
                                        </li>
                                    </ul>
                                    </nav>
                                </div>

                                <div className="chat">
                                    <div className="top">{people ?
                                    <h2>@{people[0] === this.context.state.userData.name ? people[1] : people[0]}</h2>
                                    : 'Select a person to talk to!'}
                                    </div>
                                    <div ref={this.messagesEnd} className="chatarea">
                                        
                                        {
                                            !messages ? null
                                            : messages.slice(0).reverse().map((msg, i) => {
                                                return (
                                                    <div key={i} className="msg">
                                                        <div>
                                                            <p><b>{msg.author}</b></p>
                                                            <span>{msg.date}</span>
                                                        </div>
                                                        <div><p className="msgtxt">{msg.text}</p></div>
                                                    </div>
                                                )
                                            })
                                        }
                                        
                                    </div>
                                    <div className="type">
                                    {this.state.convoid ?
                                    <form onSubmit={this.onSend}>
                                        <input ref={this.inputRef} type="text" placeholder={
                                            people ?
                                                `Message @${people[0] === this.context.state.userData.name ? people[1] : people[0]}` : null
                                        } value={this.state.message} onChange={this.onChange} name='message'/>
                                    </form> : null}
                                    </div>
                                </div>
                            </main>
                        </div>
                        
                </div>
            </div>
        )
    }
}

export default Chat
Chat.contextType = MyContext