import React, {Component} from 'react';
import {Redirect, Route, BrowserRouter as Router, Switch} from 'react-router-dom';

import Home from './components/Home'
import Chat from './components/Chat'

import { MyContext } from './storage'
import {
  getFromStorage
} from './utils/storage';

import './App.css';

export class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      error: '',
      token: this.props.token
    };

    this.setParentToken = this.setParentToken.bind(this);
  }

  setParentToken(t) {
    this.setState({ token: t });
  }

  componentDidMount() {
    const obj = getFromStorage('Epicenter');
    if (obj && obj.token) {
      fetch(`${this.context.state.proxy}users/verify?token=${obj.token}`)
        .then(res => res.json())
        .then(json => {
          if (json.success) {
            this.context.changeToken(obj.token);
            this.context.setUserData(json.user);
            console.log(json.user);
            //console.log(this.context.state.userData);
            this.setState({
              token: obj.token,
              isLoading: false
            })
          } else {
            this.setState({
              isLoading: false
            });
          }
        })
    } else {
      this.setState({
        isLoading: false
      });
    }
  
  }

  render() {
    if (this.state.isLoading) {
      return (
        <div style={
          {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '100vh',
            backgroundColor: '#333'
          }
        }>
          <h1 style={{color: 'white'}}>Loading...</h1>
        </div>
      )
    }

    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route path="/" exact strict render={
            () => (

              <React.Fragment>
                {
                  this.state.token === '' ? <Home changeToken={(token) => this.setState({ token: token })} register={false} />
                    : <Redirect to="/chat" />
                }
              </React.Fragment>
            )
          } />

          <Route path="/chat" exact component={
            (props) => (

              this.state.token !== '' ? <Chat userData={this.context.state.userData} setParentToken={this.setParentToken} {...props} />
                : <Redirect to="/" />
            )
          } />
          
          <Route path="/chat/:id" exact strict component={
            (props) => (

              this.state.token !== '' ? <Chat userData={this.context.state.userData} setParentToken={this.setParentToken} {...props} />
                : <Redirect to="/" />
            )
          } />

          
        </Switch>
      </Router>
    )
  }
}

export default App;
App.contextType = MyContext
